      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="count1" label="1" width="200">
      </el-table-column>
      <el-table-column sortable prop="count2" label="2" width="200">
      </el-table-column>
      <!-- <el-table-column sortable prop="count3" label="3" width="300">
        <template slot-scope="scope">
          <div>{{scope.row.editTime|timestampToTime}}</div>
        </template>
      </el-table-column> -->
      <el-table-column sortable prop="count3" label="3" width="200">
      </el-table-column>
      <el-table-column sortable prop="count4" label="4" width="200">
      </el-table-column>
      <el-table-column sortable prop="count5" label="5" width="200">
      </el-table-column>
      <el-table-column align="center" label="" min-width="200">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
        <el-form-item label="1" prop="count1">
          <el-input size="small" v-model="editForm.deptName" auto-complete="off" placeholder="1"></el-input>
        </el-form-item>
        <el-form-item label="2" prop="deptNo">
          <el-input size="small" v-model="editForm.deptNo" auto-complete="off" placeholder="2"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog"></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { countList, countSave, countDelete } from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      editForm: {
        deptId: '',
        deptName: '',
        deptNo: '',
        token: localStorage.getItem('logintoken')
      },
      // rules
      rules: {
        countName: [
          { required: true, message: '1', trigger: 'blur' }
        ],
        countNo: [{ required: true, message: '2', trigger: 'blur' }]
      },
      formInline: {
        page: 1,
        limit: 10,
        varLable: '',
        varName: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
