            count5: '5',
            // parentId: 1
          },
          {
            // addUser: 'gsd',
            // editUser: 'gsd',
            // addTime: 1521062371000,
            // editTime: 1526700200000,
            count1: '1',
            count2: '2',
            count3: '3',
            count4: '4',
            count5: '5',
            // parentId: 1
          },
          {
            // addUser: 'gsd',
            // editUser: 'gsd',
            // addTime: 1521062371000,
            // editTime: 1526700200000,
            count1: '1',
            count2: '2',
            count3: '3',
            count4: '4',
            count5: '5',
            // parentId: 1
          },
          {
            // addUser: 'gsd',
            // editUser: 'gsd',
            // addTime: 1521062371000,
            // editTime: 1526700200000,
            count1: '1',
            count2: '2',
            count3: '3',
            count4: '4',
            count5: '5',
            // parentId: 1
          },
          {
            // addUser: 'gsd',
            // editUser: 'gsd',
            // addTime: 1521062371000,
            // editTime: 1526700200000,
            count1: '1',
            count2: '2',
            count3: '3',
            count4: '4',
            count5: '5',
            // parentId: 1
          },
          {
            // addUser: 'gsd',
            // editUser: 'gsd',
            // addTime: 1521062371000,
            // editTime: 1526700200000,
            count1: '1',
            count2: '2',
            count3: '3',
            count4: '4',
            count5: '5',
            // parentId: 1
          },
          {
            // addUser: 'gsd',
            // editUser: 'gsd',
            // addTime: 1521062371000,
            // editTime: 1526700200000,
            count1: '1',
            count2: '2',
            count3: '3',
            count4: '4',
            count5: '5',
            // parentId: 1
          },
          {
            // addUser: 'gsd',
            // editUser: 'gsd',
            // addTime: 1521062371000,
            // editTime: 1526700200000,
            count1: '1',
            count2: '2',
            count3: '3',
            count4: '4',
            count5: '5',
            // parentId: 1
          },
          {
            // addUser: 'gsd',
            // editUser: 'gsd',
            // addTime: 1521062371000,
            // editTime: 1526700200000,
            count1: '1',
            count2: '2',
            count3: '3',
            count4: '4',
            count5: '5',
            // parentId: 1
          },
        ]
      }
      this.loading = false
      this.listData = res.data
      this.pageparm.currentPage = this.formInline.page
      this.pageparm.pageSize = this.formInline.limit
      this.pageparm.total = res.count
      // 

      /***
       *  
       */
      // deptList(parameter)
      //   .then(res => {
      //     this.loading = false
      //     if (res.success == false) {
      //       this.$message({
      //         type: 'info',
      //         message: res.msg
      //       })
      //     } else {
      //       this.listData = res.data
      //       // 
      //       this.pageparm.currentPage = this.formInline.page
      //       this.pageparm.pageSize = this.formInline.limit
      //       this.pageparm.total = res.count
      //     }
      //   })
      //   .catch(err => {
      //     this.loading = false
      //     this.$message.error('')
      //   })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      this.getdata(this.formInline)
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true
      if (row != undefined && row != 'undefined') {
        this.title = ''
        this.editForm.countId = row.countId
        this.editForm.countName = row.countName
        this.editForm.countNo = row.countNo
      } else {
        this.title = ''
        this.editForm.countId = ''
        this.editForm.countName = ''
        this.editForm.countNo = ''
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          deptSave(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.success) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: ''
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('')
            })
        } else {
          return false
        }
      })
    },
    // 
    deleteUser(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          countDelete(row.countId)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 
 


