/**
 * 
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="searchForm" class="search-form">
      <el-form-item label="">
        <el-input type="text" size="small" v-model="searchText.countname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="searchText.roleid" placeholder="ID"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" @click="handleSearch"></el-button>
        <el-button size="small" type="primary" @click="handleReset"></el-button>
      </el-form-item>
    </el-form>
    <el-form :inline="true" :model="formInline" class="user-warnning">
      <el-form-item>
        <el-button size="small" type="danger" ></el-button>
        <el-button size="small" type="success"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="mini" :data="filteredData.slice((currentPage-1)*pageSize, currentPage*pageSize)" :filter-method="handleFilter" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="selection" width="30">
      </el-table-column>
      <el-table-column  prop="sever" label="" width="100" :filters="nameFilters" :filter-method="filterByName">
      </el-table-column>
      <el-table-column  prop="countname" label="" width="100">
      </el-table-column>
      <el-table-column  prop="namecount" label="" width="100">
      </el-table-column>
      <el-table-column  prop="roleid" label="id" width="100">
      </el-table-column>
      <el-table-column  prop="rolename" label="" width="100">
      </el-table-column>
      <el-table-column  prop="rolecareer" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="rolelevel" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="outtime" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="money_30" label="30" width="100">
      </el-table-column>
      <el-table-column sortable prop="summoney" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="evalue" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="bagvalue" label="" width="100">
      </el-table-column>
      <el-table-column prop="safephone" label="" width="100">
      </el-table-column>
      <el-table-column prop="safephonecount" label="" width="100">
      </el-table-column>
      <el-table-column prop="id" label="" width="100">
      </el-table-column>
      <el-table-column prop="idcount" label="" width="100">
      </el-table-column>
      <el-table-column prop="realname" label="" width="100">
      </el-table-column>
      <el-table-column prop="lastip" label="ip" width="100">
      </el-table-column>
      <el-table-column prop="lastipcount" label="ip" width="100">
      </el-table-column>
      <el-table-column prop="lastmac" label="mac" width="100">
      </el-table-column>
      <el-table-column sortable prop="taskcount" label="-" width="100">
      </el-table-column>

      
      <el-table-column align="center"  fixed="right" label="" min-width="100">
        <template slot-scope="scope">
          <el-button type="danger" @click="deleteUser(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination @size-change="handleSizeChange"  @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 50]" :page-size="pageSize" :total="filteredData.length" layout="total, sizes, prev, pager, next, jumper"></el-pagination>
  </div>
</template>

<script>
export default {
    computed: {
        deletedData() {
            return this.$store.state.originalData
    }
  },
    data() {
    return {
      // 
      searchText: {
        countname: "",
        roleid: ""
      },
      currentPage: 1, // 
      pageSize: 10,// 
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      userparm: [], //
      // 
      nameFilters: [],
    //   deletedData: [
    //   {
    //         sever: '241',
    //         countname: 'test001',
    //         namecount: '10',
    //         roleid: '0001',
    //         rolename: '',
    //         rolecareer: '',
    //         rolelevel: '54',
    //         outtime: '2023-02-26 18:01:23',
    //         money_30: '',
    //         summoney: '',
    //         evalue: '0.0',
    //         bagvalue: '13.09',
    //         safephone: '159****4286',
    //         afephonecount: '10',
    //         id: '232126********0562',
    //         idcount: '10',
    //         realname: '*',
    //         lastip: '60.219.155.249',
    //         lastipcount: '10',
    //         lastmac: '76012d516f62',
    //         taskcount: '12',
    //       },
    //       {
    //         sever: '241',
    //         countname: 'test002',
    //         namecount: '10',
    //         roleid: '0002',
    //         rolename: '',
    //         rolecareer: '',
    //         rolelevel: '54',
    //         outtime: '2023-02-26 18:01:23',
    //         money_30: '',
    //         summoney: '',
    //         evalue: '0.0',
    //         bagvalue: '13.09',
    //         safephone: '159****4286',
    //         afephonecount: '10',
    //         id: '232126********0562',
    //         idcount: '10',
    //         realname: '*',
    //         lastip: '60.219.155.249',
    //         lastipcount: '10',
    //         lastmac: '76012d516f62',
    //         taskcount: '12',
    //       },
    //       {
    //         sever: '241',
    //         countname: 'test003',
    //         namecount: '10',
    //         roleid: '0003',
    //         rolename: '',
    //         rolecareer: '',
    //         rolelevel: '54',
    //         outtime: '2023-02-26 18:01:23',
    //         money_30: '',
    //         summoney: '',
    //         evalue: '0.0',
    //         bagvalue: '13.09',
    //         safephone: '159****4286',
    //         afephonecount: '10',
    //         id: '232126********0562',
    //         idcount: '10',
    //         realname: '*',
    //         lastip: '60.219.155.249',
    //         lastipcount: '10',
    //         lastmac: '76012d516f62',
    //         taskcount: '12',
    //       },
    //   ],
      filteredData: []
    };

  },
  created() {
    this.filteredData = [...this.deletedData] // data
  },

  mounted() {
    this.initFilters();
    this.total = this.filteredData.length;
  },

  methods: {
    initFilters() {
      // 
      this.nameFilters = this.generateFilters('sever');
    },
    generateFilters(prop) {
      let filters = [];
      // 
      this.filteredData.forEach(item => {
        if (filters.indexOf(item[prop]) === -1) {
          // filters
          filters.push(item[prop]);
        }
      });
      // 
      filters.sort();
      // filters
      return filters.map(item => {
        return {
          text: item,
          value: item
        };
      });
    },
    filterByName(value, row) {
      // 
      if (value.length === 0) {
        return true;
      }
      // 
      return value.indexOf(row.sever) !== -1;
    },

    handleFilter(value, row, column) {
      if (!value) {
      // 
        return true;
      } else {
      // 
        const prop = column['property'];
        const val = row[prop];
        return val.indexOf(value) !== -1;
      }
    },

    handleSearch() {
      const { countname, roleid} = this.searchText;
    //  filter 
      this.filteredData = this.filteredData.filter((item) => 
        (countname === "" || item.countname.includes(countname)) &&
        (roleid === "" || item.roleid.toString().includes(roleid)));
    // data
    },

    handleReset() {
    // 
      this.searchText.countname = '';
      this.searchText.roleid = '';
      this.filteredData = this.deletedData// 
    },

    deleteUser(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          countDelete(row.count2)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },

    handleSizeChange(val) {
      this.pageSize = val
      this.currentPage = 1 //  1
    },

    handleCurrentChange(val) {
      this.currentPage = val
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 
 


