    options2() {
      if (this.selected1 === 'option2') {
        return [
          { value: 'option001', label: '001 - ' },
          { value: 'option002', label: '002 - ' },
          { value: 'option003', label: '003 - ,,,' },
          { value: 'option004', label: '004 - 18117313852 ' },
          { value: 'option005', label: '005 -  ' },
        ];
      } else if (this.selected1 === 'option3') {
        return [
          { value: 'option010', label: '010 -  ' },
          { value: 'option011', label: '011 -  ' },
          { value: 'option012', label: '012 -  ' },
        ];
      } else {
        return [];
      }
    }
