  created() {
    // data
    this.filteredData = [...this.originalData]
    // this.state.dispatch('loadData')
  },

  mounted() {
    this.initFilters();
    this.total = this.filteredData.length;
  },

  methods: {
    formatNumber(row, column) {
      return row.evalue;
    },
    getCellClassName({ row }) {
      return row.evalue > 150 ? 'red' : '';
    },
    initFilters() {
      // 
      this.nameFilters = this.generateFilters('sever');
    },
    generateFilters(prop) {
      let filters = [];
      // 
      this.originalData.forEach(item => {
        if (filters.indexOf(item[prop]) === -1) {
          // filters
          filters.push(item[prop]);
        }
      });
      // 
      filters.sort();
      // filters
      return filters.map(item => {
        return {
          text: item,
          value: item
        };
      });
    },
    filterByName(value, row) {
      // 
      if (value.length === 0) {
        return true;
      }
      // 
      return value.indexOf(row.sever) !== -1;
    },

    handleFilter(value, row, column) {
      if (!value) {
      // 
        return true;
      } else {
      // 
        const prop = column['property'];
        const val = row[prop];
        return val.indexOf(value) !== -1;
      }
    },

    handleSearch() {
      const { countname, roleid, rolename } = this.searchText;
    //  filter 
      this.filteredData = this.originalData.filter((item) => 
        (countname === "" || item.countname.includes(countname)) &&
        (roleid === "" || item.roleid.toString().includes(roleid)));
    // data
      // this.filteredData = filteredData
    },

    handleReset() {
    // 
      this.searchText.countname = '';
      this.searchText.roleid = '';
      this.filteredData = this.originalData// 
    },

    // 
    deleteData(roleid) {
      this.filteredData = this.filteredData.filter(item => !this.selectedIds.includes(item.roleid));
      this.dialogVisible1 = false
    },

    handleSizeChange(val) {
      this.pageSize = val
      this.currentPage = 1 //  1
    },

    handleCurrentChange(val) {
      this.currentPage = val
    },

    handleSelectionChange(selection) {
      this.selectedIds = selection.map(item => item.roleid);
    },

    deleteSelected() {
      this.filteredData = this.filteredData.filter(item => !this.selectedIds.includes(item.roleid));
    },
    showModal1: function(index, row) {
      this.dialogVisible1 = true
    },
    showModal2: function(index, row) {
      this.dialogVisible2 = true
    },
    handleBan() {
      // 
      this.dialogVisible1 = false
    },
