// 
import Vue from 'vue';
import Router from 'vue-router';
// 
import login from '@/views/login';
// 
import index from '@/views/index';
// 
import Counts from '@/views/counts/Counts';
import xycounts from '@/views/counts/xycounts';
import fjcounts from '@/views/counts/fjcounts';
// 
import countmanage from '@/views/manage/countmanage';
// 
import safesecure from '@/views/system/safesecure';

// 
Vue.use(Router);

//  
export default new Router({
    routes: [{
        path: '/',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, {
        path: '/login',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, {
        path: '/index',
        name: '',
        component: index,
        iconCls: 'el-icon-tickets',
        children: [{
            path: '/counts/Counts',
            name: '',
            component: Counts,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/counts/xycounts',
            name: '',
            component: xycounts,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/counts/fjcounts',
            name: '',
            component: fjcounts,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/manage/countmanage',
            name: '',
            component: countmanage,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/system/safesecure',
            name: '',
            component: safesecure,
            meta: {
                requireAuth: true
            }
        }, 
    ]
    }]
})


